.header {
  padding: 45px 0;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 650px;
  width: 100%;
}
.header-nav {
  display: flex;
  align-items: center;
}
.header-nav__link {
  font-weight: 700;
  font-size: 18px;
  color: #303030;
  position: relative;
  margin-right: 45px;
}
.header-nav__link:after {
  left: 0;
  right: auto;
  bottom: 2px;
  top: auto;
  position: absolute;
  content: "";
  width: 100%;
  height: 8px;
  background: #54ED39;
  display: block;
  z-index: -1;
}
.header-nav__link:last-child {
  margin-right: 0;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html {
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box;
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
  min-width: 320px;
  min-height: 100%;
  font-family: "Roboto", sans-serif;
}

ul, li, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

address, caption, cite, code, dfn, em, th, var {
  font-style: normal;
  font-weight: normal;
}

label {
  cursor: pointer;
}

hr {
  box-sizing: content-box;
  overflow: visible;
  height: 0;
}

details, legend {
  display: block;
}

summary {
  display: list-item;
}

b, strong {
  font-weight: bolder;
}

button {
  background-color: transparent;
  display: inline-block;
  font-family: inherit;
  font-size: inherit;
  box-shadow: none;
  cursor: pointer;
  border: none;
  padding: 0;
}

input {
  border-radius: 0;
  outline: none;
  margin: 0;
  border: 0;
}
input::-ms-clear, input::-webkit-search-decoration, input::-webkit-search-cancel-button, input::-webkit-search-results-button, input::-webkit-search-results-decoration {
  display: none;
}

input, textarea {
  outline: none;
  resize: none;
}

textarea, select {
  font-family: inherit;
  font-size: inherit;
}

progress {
  vertical-align: baseline;
}

img, svg, video {
  display: inline-block;
  vertical-align: top;
  object-fit: cover;
  max-width: 100%;
  height: auto;
}

fieldset, img {
  border: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  line-height: inherit;
  font-size: inherit;
  margin: 0;
}

div, dl, dt, dd, pre, form, fieldset, blockquote, th, td, p {
  margin: 0;
}

a {
  cursor: pointer;
  text-decoration: none;
}

