.btn {
  border-color: rgba(0, 0, 0, 0.2);
  border-style: solid;
  border-radius: 0;
  border-width: 1px;
  max-width: 220px;
  max-height: 65px;
  width: 100vh;
  height: 100vh;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
  transition: 0.3s all;
}
.btn-fill--white {
  background-color: #ffffff;
}
.btn-fill__text {
  font-weight: 700;
  font-size: 18px;
  color: #303030;
}

.sidebar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 25px;
  height: 365px;
  left: 60px;
  right: auto;
  bottom: auto;
  top: 240px;
  position: fixed;
  content: "";
  flex-direction: column;
}
.sidebar-content {
  width: 120px;
  height: 120px;
}
.sidebar-content__subtitle {
  font-weight: 400;
  font-size: 14px;
  color: #303030;
  display: flex;
  white-space: nowrap;
  transform: rotate(-90deg) translateX(-50px);
}
.sidebar__line {
  width: 1px;
  height: 165px;
  background-color: #303030;
}
.sidebar-link {
  transition: all 0.3s ease-in-out;
}
.sidebar-link__img {
  width: 24px;
  height: 24px;
  fill: #303030;
  object-fit: cover;
}
.sidebar-link:hover {
  transform: scale(1.2);
}